.online {
    display: flex;
    overflow: hidden;
    padding: clamp(30px, calc(90vw/ 14.4),120px) 0;
    @include media($xs) {
        flex-direction: column-reverse;
        align-items: center;
    }


    &__content {
        flex: 1 0 100%;
        display: flex;
        flex-direction: column;
        padding-bottom: clamp(50px, calc(200vw/ 14.4), 250px);
    }

    &__body {
        width: 35%;
        color: $text;
        @include media($xs) {
            width: 100%;
            text-align: center;
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }

    &__uptitle {
        font-size: clamp(10px,calc(14vw /14.4), 20px);
        line-height: clamp(15px, calc(20vw/ 14.4) ,25px);
        margin-bottom: clamp(10px, calc(25vw /14.4) ,30px);
    }

    &__title {
        font-size: clamp(15px, calc(30vw/ 14.4) , 35px);
        line-height: clamp(20px, calc(40vw/ 14.4),50px);
        margin-bottom: clamp(10px, calc(25vw / 14.4), 30px);
    }

    &__text {
        font-size: clamp(12px, calc(16vw /14.4) , 20px);
        line-height: clamp(20px, calc(24vw / 14.4), 32px);
        margin-bottom: clamp(10px, calc(25vw / 14.4), 30px);
    }

    &__link {
        width: clamp(150px, calc(300vw/ 14.4), 350px);
        height: clamp(30px, calc(60vw/ 14.4), 80px);
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: clamp(10px,calc(14vw /14.4), 20px);
        line-height: clamp(10px,calc(14vw /14.4), 20px);
        border: 1px solid rgba(51, 51, 51, 0.2);
        color: $text;
        @include media($xs) {
            padding: 0 10px;
        }
    }

    &__image {
        position: relative;
        /* flex: 0 0 calc(((100vw - общий padding) * процентная ширина) + половина обшего padding); */
        flex: 0 0 calc(((100vw - 30px) * 0.6) + 15px);
        transform: translate(-100%,0);
        @include media($xs) {
            padding-right: 100%;
            transform: translate(0,0);
        }

        & img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

}