.header {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    background: url('../images/header-bg.jpg') right center / cover no-repeat;
    position: relative;


    &-ad,
    &-subad {
        padding: 10px 0;
        background: $first;
        color: $white;
        display: flex;
        gap: 10px;
        justify-content: center;
        flex-wrap: wrap;
        text-align: center;
        font-size: clamp(10px, calc(14vw / 14.4),20px);
        line-height: clamp(15px, calc(20vw / 14.4),25px);
    }
    
    &-subad {
        gap: 0;
        background: $second;
    }

    &__nav {
        display: flex;
        justify-content: space-between;
        padding-top: 35px;
        align-items: center;
        z-index: 999;

        @include media($md) {
            position: fixed;
            flex-direction: column;
            width: 60%;
            height: 100%;
            background: $white;
            top: 0;
            right: -100%;
            justify-content: center;
            align-items: center;
        }
        &.active {
            right: 0;
        }
        
    }
    &__list {
        display: flex;
        align-items: center;
        @include media($md) {
            flex-direction: column;
        }
        
    }
    &__link {
        display: inline-block;
        margin-left: 25px;
        color: $white;
        font-size: 14px;
        line-height: 24px;

        @include media($md) {
            font-size: clamp(24px, calc(32vw / 14.4) , 40px);
            line-height: clamp(24px, calc(40vw / 14.4), 45px);
            margin: 20px 0 20px 0;
            color: $black;
        }
    }

    &__item {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        
        &-body {
            display: flex;
            flex-direction: column;
            width: 40%;
            color: $white;
            @include media($lg) {
                width: 80%;
            } 
            @include media($md) {
                width: 100%;
            }

        }
    }

    &__burger {
        width: 50px;
        height: 50px;
        position: fixed;
        // background: yellow;
        top: 5px;
        right: 5px;   
        pointer-events: visible;
        display: none;
        z-index: 999;
        @include media($md) {
            display: inline-block;
        }

        & span {
            width: 100%;
            height: 5px;
            background: $white;
            position: absolute;
            border-radius: 10px;
            transition: 0.5s;
        }
        &_1 {
            top: 5px;
        }
        &_2 {
            top: 22px;
        }
        &_3 {
            bottom: 5px;
        }

        &.active span {
            background: $black;
        }
        &.active &_2 {
            transform: scaleX(0);
            transition: 0.1s;
        }
        &.active &_1 {
            top: 25px;
            transform: rotate(45deg);
        }
        &.active &_3 {
            transform: rotate(-45deg);
            bottom: 20px;
        }
    }



    .header__title {
        font-size: 30px;
        line-height: 40px;
        margin-bottom: 15px;
    }
    .header__text {
        font-size: 16px;
        line-height: 27px;
    }
    

}


button {
    background: $black;
    position: absolute;
    top: 0;
    left: 0;
    padding: 10px 20px;
    color: $white;
    border: none;
}