@charset "UTF-8";
@font-face {
  font-family: "Roboto";
  src: url(../fonts/Roboto/Roboto-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: "Roboto";
  src: url(../fonts/Roboto/Roboto-Bold.ttf);
  font-weight: 700;
}

@font-face {
  font-family: "Roboto";
  src: url(../fonts/Roboto/Roboto-Black.ttf);
  font-weight: 900;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto";
}

:root {
  filter: invert(0) hue-rotate(0);
}

img {
  filter: invert(0) hue-rotate(0);
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

html {
  overflow-x: hidden;
}

body {
  position: relative;
  overflow-x: hidden;
  background: #fffef2;
}

body::before {
  content: '';
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.651);
  position: absolute;
  z-index: 998;
  top: 0;
  left: 0;
  display: none;
}

body.active::before {
  display: block;
}

.container {
  max-width: 1281px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-right: 15px;
  padding-left: 15px;
}

@media (max-width: 1281px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

::after, ::before {
  font-family: 'fontawesome';
}

.icon-film::before {
  content: '\f008';
  font-weight: 300;
}

.header {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  background: url("../images/header-bg.jpg") right center/cover no-repeat;
  position: relative;
}

.header-ad, .header-subad {
  padding: 10px 0;
  background: #a6672b;
  color: #ffffff;
  display: flex;
  gap: 10px;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;
  font-size: clamp(10px, calc(14vw / 14.4), 20px);
  line-height: clamp(15px, calc(20vw / 14.4), 25px);
}

.header-subad {
  gap: 0;
  background: #252525;
}

.header__nav {
  display: flex;
  justify-content: space-between;
  padding-top: 35px;
  align-items: center;
  z-index: 999;
}

@media (max-width: 992px) {
  .header__nav {
    position: fixed;
    flex-direction: column;
    width: 60%;
    height: 100%;
    background: #ffffff;
    top: 0;
    right: -100%;
    justify-content: center;
    align-items: center;
  }
}

.header__nav.active {
  right: 0;
}

.header__list {
  display: flex;
  align-items: center;
}

@media (max-width: 992px) {
  .header__list {
    flex-direction: column;
  }
}

.header__link {
  display: inline-block;
  margin-left: 25px;
  color: #ffffff;
  font-size: 14px;
  line-height: 24px;
}

@media (max-width: 992px) {
  .header__link {
    font-size: clamp(24px, calc(32vw / 14.4), 40px);
    line-height: clamp(24px, calc(40vw / 14.4), 45px);
    margin: 20px 0 20px 0;
    color: #000000;
  }
}

.header__item {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.header__item-body {
  display: flex;
  flex-direction: column;
  width: 40%;
  color: #ffffff;
}

@media (max-width: 1281px) {
  .header__item-body {
    width: 80%;
  }
}

@media (max-width: 992px) {
  .header__item-body {
    width: 100%;
  }
}

.header__burger {
  width: 50px;
  height: 50px;
  position: fixed;
  top: 5px;
  right: 5px;
  pointer-events: visible;
  display: none;
  z-index: 999;
}

@media (max-width: 992px) {
  .header__burger {
    display: inline-block;
  }
}

.header__burger span {
  width: 100%;
  height: 5px;
  background: #ffffff;
  position: absolute;
  border-radius: 10px;
  transition: 0.5s;
}

.header__burger_1 {
  top: 5px;
}

.header__burger_2 {
  top: 22px;
}

.header__burger_3 {
  bottom: 5px;
}

.header__burger.active span {
  background: #000000;
}

.header__burger.active .header__burger_2 {
  transform: scaleX(0);
  transition: 0.1s;
}

.header__burger.active .header__burger_1 {
  top: 25px;
  transform: rotate(45deg);
}

.header__burger.active .header__burger_3 {
  transform: rotate(-45deg);
  bottom: 20px;
}

.header .header__title {
  font-size: 30px;
  line-height: 40px;
  margin-bottom: 15px;
}

.header .header__text {
  font-size: 16px;
  line-height: 27px;
}

