  line-height: clamp(20px, calc(30vw /14.4), 40px);
  margin-top: -9px;
}

.footer__block-6 h2::after {
  content: '';
  width: 100%;
  height: 1px;
  background: #ffffff;
  display: block;
  margin-top: 17px;
  margin-bottom: 15px;
}

.footer__block-6 a {
  font-size: clamp(10px, calc(14vw /14.4), 20px);
  line-height: clamp(20px, calc(30vw /14.4), 40px);
  color: #ffffff;
}

