* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Roboto";
}

:root {
    filter: invert(0) hue-rotate(0);
}

img {
    filter: invert(0) hue-rotate(0);
}

a { 
    text-decoration: none;
}

ul {
    list-style: none;
}

html {
    overflow-x: hidden;
}

body {
    position: relative;
    overflow-x: hidden;
    background: $body;
}

body::before {
    content: '';
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.651);
    position: absolute;
    z-index: 998;
    top: 0;
    left: 0;
    display: none;
}

body.active::before {
    display: block;
}

.container {
    @include container();
}

::after , ::before {
    font-family: 'fontawesome';
} 

.icon {
    &-film::before {
        content: '\f008';
        font-weight: 300;
        
    }
}