$lg: 1281px;
$md: 992px;
$sm: 768px;
$xs: 576px;
$gutter: 30px;
$gutter-side: $gutter / 2;
$column: 12;

@mixin media($size: $lg) {
    @media(max-width: $size) {
        @content;
    }
}
@mixin media-min($size: $lg) {
    @media(min-width: $size) {
        @content;
    }
}

@mixin container($padding: $gutter-side) {
    max-width: 1281px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-right: $padding;
    padding-left: $padding;

    @include media() {
        max-width: $md;
    }
    @include media($md) {
        max-width: $sm;
    }
    @include media($sm) {
        max-width: $xs;
    }
    @include media($xs) {
        max-width: 100%;
    }
}