.saying {
    padding: clamp(25px, calc(100vw /14.4), 150px) 0 clamp(50px,calc(200vw/ 14.4), 250px) 0;


    &__body {
        display: flex;
        flex-direction: column;
        color: $text;
        justify-content: center;
        align-items: center;
        width: 55%;
        margin: 0 auto;
        text-align: center;
        @include media($xs) {
            width: 100%;
        }
    }

    &__title {
        font-size: clamp(20px, calc(30vw /14.4) ,40px);
        line-height: clamp(30px, calc(40vw/ 14.4),50px);
        margin-bottom: clamp(5px, calc(15vw/ 14.4),20px);
    }
    &__text {
        font-size: clamp(10px, calc(14vw/ 14.4), 20px);
        line-height: clamp(15px, calc(28vw /14.4),35px);
    }
}