$Roboto: ("Roboto", "Regular", 400), 
            ("Roboto", "Bold", 700),
            ("Roboto", "Black", 900);

            

@each $family, $name, $weight in $Roboto {
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}-#{$name}.ttf);
        font-weight: $weight;
    }
}   

$black: #000000;
$white: #ffffff;
$first: rgba(166, 103, 43, 1);
$second:rgba(37, 37, 37, 1);
$body: rgba(255, 254, 242, 1);
$text: rgba(51, 51, 51, 1);




