.produce {
    padding: clamp(50px, calc(100vw/ 14.4), 150px) 0;
    background: $body;
    cursor: move;

    &__wrapper {
        display: flex;

        
    }

    &__body {
        @include media($xs) {
            overflow: hidden;
        }
    }

    &__slide {
        display: flex;
        flex: 1 0 100%;
        width: 100%;

        @include media($xs) {
            flex-wrap: wrap;
            justify-content: space-around;
        }
    }
    &__card {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;

        @include media($xs) {
            margin: 15px 0;
        }
    }
    &__img {
        width: clamp(210px, calc(420vw/ 14.4),500px);
        height: clamp(200px, calc(400vw/ 14.4), 510px);
        max-width: 100%;
        object-fit: cover;
    }
    &__text,
    &__subtext {
        font-size: clamp(10px, calc(14vw/14/4),20px);
        line-height: clamp(15px, calc(24vw/ 14.4), 30px);
        color: $text;
        margin-top: clamp(5px, calc(10vw/ 14.4),15px);
    }

    &__scrollbar {
        width: 100%;
        margin-top: clamp(25px, calc(50vw / 14.4), 60px);
        height: 2px;
        background: rgba(0, 0, 0, 0.2);
    }

    &__drag {
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0,0.6);
    }

}