.footer {
    background: $second;
    padding: 60px 0;
    &__body {
        display: flex;
        flex-direction: column;
    }

    &__up {
        display: grid;
        grid-template: auto / 2fr 1fr 1fr 1fr;
        column-gap: clamp(16px, calc(40vw /14.4), 50px);
        color: $white;
        @include media() {
            display: flex;
            flex-direction: column;
        }
    }

    &__bottom {
        display: grid;
        grid-template: auto / 2fr 1fr 1fr 1fr;
        column-gap: clamp(16px, calc(40vw /14.4), 50px);
        color: $white;
        @include media() {
            display: flex;
            flex-direction: column;
        }
    }

    &__block-1 {
        grid-column: 1 / 2;
        margin: 30px 0;
        
        & h2 {
            width: 100%;
            padding-left: 10px;
            border: 1px solid $white;
            font-size: clamp(15px, calc(20vw /14.4),25px);
            line-height: clamp(18px, calc(36vw /14.4),44px);

        }

        & p {
            font-size: clamp(10px, calc(14vw /14.4), 20px);
            line-height: clamp(18px, calc(26vw /14.4), 34px);
            margin-top: 10px;
        }
    }
    &__block-2 {
        grid-column: 2 / 3;
        margin: 30px 0;
        display: flex;
        flex-direction: column;

        & h2 {
            font-size: clamp(10px, calc(15vw /14.4), 20px);
            line-height: clamp(20px, calc(30vw/ 14.4), 40px);
            margin-top: -9px;

            &::after {
                content: '';
                width: 100%;
                height: 1px;
                background: $white;
                display: block;
                margin-top: 17px;
                margin-bottom: 15px;
            }
        }

        & a {
            font-size: clamp(10px, calc(14vw /14.4), 20px);
            line-height: clamp(20px, calc(30vw /14.4) , 40px);
            color: $white;
        }

    }
    &__block-3 {
