.start {
    display: flex;
    overflow: hidden;
    @include media(){
        flex-direction: column;
        align-items: center;
        background: $body;
    }

    &__image {
        position: relative;
        display: flex;
        flex: 1 0 100%;
        // padding-right: 100%;
        @include media(){
            padding-right: 100%;
            padding-bottom: 100%;
        }

        & img {
            position: absolute;
            top: 0;
            left: 0;
            width: 50%;
            height: 100%;
            object-fit: cover;
            @include media(){
                width: 100%;
            }
        }
    }

    &__content {
        flex: 1 0 calc(((100vw - 30px) * 0.5) + 15px);
        transform: translate(-100%,0);
        background: $body;
        @include media($lg){
            /* flex: 0 0 calc( 50vw - ( процент картинки - 0.5 ) * ширина контейнера); */
            flex: 0 0 calc( 50vw - (0.5 - 0.5) * 1281px);
        }
        @include media(){
            transform: translate(0,0);
        }
    }


    &__img {
        max-width: 100%;
        width: clamp(75px, calc(150vw / 14.4), 200px);
        height: clamp(100px, calc(200vw / 14.4), 250px);
        @include media($sm) {
            margin-top: 70px;
            display: inline-block;
            width: clamp(150px, calc(300vw / 14.4), 400px);
            height: clamp(200px, calc(400vw / 14.4), 500px);
        }
    }

    &__body {
        display: flex;
        align-items: center;
        gap: clamp(20px, calc(40vw/ 14.4), 60px);
        padding: 0 clamp(20px, calc(40vw / 14.4) , 60px);
        position: relative;

        @include media($sm) {
            flex-direction: column;
            text-align: center;
        }
    }

    &__content-body {
        display: flex;
        flex-direction: column;
        columns: $text;
        padding: clamp(80px, calc(160vw / 14.4) ,200px) 0;

        @include media($sm) {
            align-items: center;
            padding: 20px 0;
        }
    }
    &__title {
        font-size: clamp(20px, calc(28vw/ 14.4),34px);
        line-height: clamp(25px, calc(35vw / 14.4), 40);
        margin-bottom: clamp(10px, calc(20vw / 14.4), 30px);
    }
    &__subtitle {
        font-size: clamp(12px, calc(16vw/ 14.4), 20px);
        line-height: clamp(18px, calc(24vw/ 14.4), 25px);
        margin-bottom: clamp(10px, calc(20vw / 14.4), 30px);
    }
    &__text {
        font-size: clamp(10px, calc(14vw/ 14.4), 20px);
        line-height: clamp(16px, calc(20px / 14.4) , 25px );
    }

    &__link {
        font-size: clamp(10px, calc(14vw/ 14.4), 20px);
        line-height: clamp(10px, calc(14vw/ 14.4), 20px);
        width: clamp(200px, calc(400vw/ 14.4), 500px);
        height: clamp(30px, calc(60vw/ 14.4), 80px);
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        margin-top: clamp(10px, calc(20vw/ 14.4) ,30px);

        &-first {
            color: $white;
            background: $text;
        }
        &-second {
            color: $text;
            background: transparent;
            border: 1px solid $text;
        }
    }

    &__span {
        position: absolute;
        top: clamp(20px, calc(40vw/ 14.4) , 50px);
        right: clamp(10px, 20vw/ 14.4) ,30px;
        display: flex;
        flex-direction: column;

        &::after {
            content: '';
            display: block;
            width: 100%;
            height: 1px;
            background: $text;
        }
    }
}